'use client';

import { useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import { Sparkles, Gamepad2, ArrowRight, User, Mail, Lock } from 'lucide-react';
import Link from 'next/link';
import { useAuth } from '@/context/AuthContext';
import { useRouter } from 'next/navigation';

export default function LoginPage() {
  const [phoneNumber, setPhoneNumber] = useState('');
  const [codename, setCodename] = useState('');
  const { user, loading, signInWithGoogle, signInWithFacebook, registerWithEmail, loginWithEmail } = useAuth();
  const router = useRouter();

  // Email Auth State
  const [isLoginMode, setIsLoginMode] = useState(true);
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [name, setName] = useState('');
  const [authLoading, setAuthLoading] = useState(false);

  // Redirect to dashboard if already logged in
  useEffect(() => {
    if (user && !loading) {
      router.push('/dashboard');
    }
  }, [user, loading, router]);

  const handleEmailAuth = async () => {
    if (!email || !password) return alert("Please fill in email and password");

    setAuthLoading(true);
    try {
      if (isLoginMode) {
        await loginWithEmail(email, password);
      } else {
        if (!name) return alert("Please enter your name");
        await registerWithEmail(email, password, name);
        alert("Account created! Please check your email for verification.");
      }
    } catch (error: any) {
      alert(error.message);
    } finally {
      setAuthLoading(false);
    }
  };

  const handleGoogleLogin = async () => {
    await signInWithGoogle();
  };

  const handleFacebookLogin = async () => {
    await signInWithFacebook();
  };

  return (
    <div className="min-h-screen bg-[#1F2E4A] flex flex-col items-center justify-center p-6 relative overflow-hidden">
      {/* Background Elements */}
      <div className="absolute top-0 left-0 w-full h-full pointer-events-none opacity-20 bg-[url('https://www.transparenttextures.com/patterns/cubes.png')] animate-pulse"></div>
      <div className="absolute top-10 left-10 w-32 h-32 bg-[#7AC0E7] rounded-full blur-[60px] opacity-40 animate-bounce"></div>
      <div className="absolute bottom-10 right-10 w-40 h-40 bg-[#FFD700] rounded-full blur-[60px] opacity-30"></div>

      {/* Main Card */}
      <motion.div
        initial={{ opacity: 0, scale: 0.9, y: 20 }}
        animate={{ opacity: 1, scale: 1, y: 0 }}
        transition={{ duration: 0.5, type: 'spring' }}
        className="w-full max-w-md glass-card p-8 bg-white/95 backdrop-blur-xl border-4 border-white shadow-2xl rounded-3xl relative z-10"
      >
        {/* Mascot / Logo Area */}
        <div className="flex justify-center -mt-20 mb-6">
          <motion.div
            animate={{ y: [0, -10, 0] }}
            transition={{ repeat: Infinity, duration: 4 }}
            className="w-32 h-32 rounded-full bg-gradient-to-br from-[#7AC0E7] to-[#4A90E2] border-4 border-white shadow-lg flex items-center justify-center relative"
          >
            <Gamepad2 className="w-16 h-16 text-white" />
            <div className="absolute -right-2 -top-2 bg-[#FFD700] p-2 rounded-full border-2 border-white">
              <Sparkles className="w-5 h-5 text-[#1F2E4A]" />
            </div>
          </motion.div>
        </div>

        <div className="text-center mb-8">
          <h1 className="text-3xl font-bold text-[#1F2E4A] mb-2 font-poppins">Up Level Guild</h1>
          <p className="text-gray-500 font-nunito">
            Welcome to the adventure! <br />
            Login to start your journey.
          </p>
        </div>

        {/* Login Buttons (Prioritized) */}
        <div className="space-y-4">
          {/* Social Login - Primary Method */}
          <div className="grid grid-cols-1 gap-3">
            <button
              onClick={handleGoogleLogin}
              className="h-14 rounded-xl bg-white border-2 border-gray-100 hover:border-blue-500 hover:bg-blue-50 transition-all flex items-center justify-center gap-2 font-bold text-gray-700 shadow-sm"
            >
              <span className="text-blue-500 text-xl">G</span> Continue with Google
            </button>
            <button
              onClick={handleFacebookLogin}
              className="h-14 rounded-xl bg-[#1877F2] text-white border-2 border-[#1877F2] hover:bg-[#166fe5] transition-all flex items-center justify-center gap-2 font-bold shadow-sm"
            >
              <span className="text-white text-xl font-bold">f</span> Continue with Facebook
            </button>
          </div>

          {/* Email / Password Section */}
          <div className="border-t border-gray-200 pt-6 mt-6">
            <div className="flex bg-gray-100 rounded-lg p-1 mb-4 relative z-20">
              <button
                onClick={() => setIsLoginMode(true)}
                className={`flex-1 py-2 text-sm font-bold rounded-md transition-all ${isLoginMode ? 'bg-white shadow text-blue-600' : 'text-gray-500 hover:text-gray-700'}`}
              >
                Login
              </button>
              <button
                onClick={() => setIsLoginMode(false)}
                className={`flex-1 py-2 text-sm font-bold rounded-md transition-all ${!isLoginMode ? 'bg-white shadow text-blue-600' : 'text-gray-500 hover:text-gray-700'}`}
              >
                Sign Up
              </button>
            </div>

            <div className="space-y-3">
              {!isLoginMode && (
                <div className="relative">
                  <User className="absolute left-3 top-3.5 text-gray-400 w-5 h-5" />
                  <input
                    type="text"
                    placeholder="Adventurer Name"
                    value={name}
                    onChange={(e) => setName(e.target.value)}
                    className="w-full h-12 rounded-xl bg-gray-50 border-2 border-gray-100 focus:border-blue-400 focus:bg-white pl-10 pr-4 outline-none transition-all text-gray-700 relative z-10"
                  />
                </div>
              )}
              <div className="relative">
                <Mail className="absolute left-3 top-3.5 text-gray-400 w-5 h-5" />
                <input
                  type="email"
                  placeholder="Email Address"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                  className="w-full h-12 rounded-xl bg-gray-50 border-2 border-gray-100 focus:border-blue-400 focus:bg-white pl-10 pr-4 outline-none transition-all text-gray-700 relative z-10"
                />
              </div>
              <div className="relative">
                <Lock className="absolute left-3 top-3.5 text-gray-400 w-5 h-5" />
                <input
                  type="password"
                  placeholder="Password"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  className="w-full h-12 rounded-xl bg-gray-50 border-2 border-gray-100 focus:border-blue-400 focus:bg-white pl-10 pr-4 outline-none transition-all text-gray-700 relative z-10"
                />
              </div>

              <button
                onClick={handleEmailAuth}
                disabled={loading}
                className="w-full h-12 mt-4 rounded-xl bg-gradient-to-r from-blue-500 to-indigo-600 text-white font-bold shadow-lg hover:shadow-xl hover:scale-[1.02] transition-all flex items-center justify-center gap-2 relative z-20"
              >
                {loading ? (
                  <div className="w-5 h-5 border-2 border-white/30 border-t-white rounded-full animate-spin" />
                ) : (
                  <>
                    {isLoginMode ? 'Enter Guild' : 'Start Adventure'} <ArrowRight className="w-5 h-5" />
                  </>
                )}
              </button>

              {!isLoginMode && (
                <p className="text-xs text-center text-gray-400 mt-2">
                  * Verification email will be sent.
                </p>
              )}

              {/* Guest Link */}
              <div className="text-center mt-4">
                <button onClick={() => router.push('/dashboard')} className="text-xs text-gray-400 hover:text-gray-600 underline">
                  Or continue as Guest (Demo)
                </button>
              </div>
            </div>
          </div>
        </div>

        <p className="text-center text-xs text-gray-400 mt-6 font-semibold">
          By joining, you agree to our <span className="text-[#7AC0E7] cursor-pointer hover:underline">Guild Rules</span>
        </p>
      </motion.div>
    </div>
  );
}
